<template>
  <div class="page">
    <div @click="start" class="start" v-if="step == 0">开始播放</div>
    <div v-show="step === 1" class="contentContainer" :class="{active: step === 1}">
      <video src="@/assets/01.mp4" ref="v1" class="video"></video>
    </div>
    <div v-show="step === 2" class="contentContainer" :class="{active: step === 2}">
      <div class="top">
        <div class="text">
          <h1>新盛和阳果蔬生鲜市场今日菜价</h1>
          <div class="category">
            <li :class="{active: item == allCategory[currentCategoryIndex]}"
              v-for="(item, index) of allCategory" :key='index'>
              {{item.name}}
              <template v-if="item == allCategory[currentCategoryIndex] && currentTotalPage > 1">
                {{currentPage}}/{{currentTotalPage}}
              </template>
            </li>
          </div>
        </div>
      </div>
      <div class="wrapper">
        <div class="table table1">
          <template v-for="(item, index) of currentShowProducts">
            <div class="cell" v-if="!isOdd(index)" :key='index'>
              <div class="l">
                <span>{{item.name}}</span>
              </div>
              <div class="m"> </div>
              <div class="r">
                <span>{{item.price || '-'}}<i>{{item.unit}}</i></span>
              </div>
            </div>
          </template>
        </div>
        <div class="table table2">
          <template v-for="(item, index) of currentShowProducts">
            <div class="cell" v-if="isOdd(index)" :key='index'>
              <div class="l">
                <span>{{item.name}}</span>
              </div>
              <div class="m"> </div>
              <div class="r">
                <span>{{item.price || '-'}}<i>{{item.unit}}</i></span>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div v-show="step === 3" class="contentContainer" :class="{active: step === 3}">
      <video src="@/assets/02.mp4" ref="v2" class="video"></video>
    </div>
  </div>
</template>

<script>
  let timer;
  let selfSwitchInterval = 5000
  import Category from "@/request/Category"
  const categoryRequest = new Category();
  import Product from "@/request/Product"
  const productRequest = new Product();
  export default {
    async created() {
      await this.getCategory()
      await this.getProducts()
    },
    mounted() {
      this.$nextTick(() => {
        this.$refs.v1.addEventListener('ended', () => {
          
          this.step++
          this.startLoop()
          this.showNext(0)
        });
        this.$refs.v2.addEventListener('ended', () => {
          
          this.step = 1
          this.$refs.v1.play()
        });
      })
    },
    data() {
      return {
        step: 0,
        allCategory: [],
        allProducts: [],
        currentCategoryIndex: 0,
        currentShowProducts: [],
        currentTotalPage: 0,
        currentPage: 0,
        loopTime: 0,
        playstart: false,
      }
    },
    methods: {
      start() {
        this.step = 1;
        this.playstart = true
        this.$refs.v1.play()
      },
      async getCategory() {
        let res = await categoryRequest.getAll()
        this.allCategory = res.rows
      },
      async getProducts() {
        this.allProducts = []
        for (const key in this.allCategory) {
          let data = {
            category_id: this.allCategory[key].id
          }
          let res = await productRequest.get(data)
          this.allProducts.push(res.rows)
        }
      },
      startLoop() {
        let categoryLength = this.allCategory.length
        clearInterval(timer)
        timer = setInterval(() => {
          if (this.currentPage == this.currentTotalPage) {
            this.currentCategoryIndex++
            this.currentPage = 0
            if (this.currentCategoryIndex >= categoryLength) {
              this.currentCategoryIndex = 0
              this.loopTime++
              if(this.loopTime >= 2) {
                this.loopTime = 0
                clearInterval(timer)
                this.step = 3
                this.$refs.v2.play()
              }
            }
          }
          this.showNext(this.currentCategoryIndex)
        }, selfSwitchInterval);
      },
      showProduct() {
        let currentProductsOfCategory = this.cutBy(this.allProducts[this.currentCategoryIndex], 20)
        this.currentTotalPage = currentProductsOfCategory.length
      },
      cutBy(arr, number) {
        arr = Array.from(arr)
        let tempArr = []
        while (arr.length !== 0) {
          let temp = arr.splice(0, number);
          tempArr.push(temp)
        }
        return tempArr
      },
      isOdd(num) {
        return num % 2 === 1 ? true : false
      },
      showNext(index) {
        let currentProductsOfCategory = this.cutBy(this.allProducts[index], 20)
        this.currentTotalPage = currentProductsOfCategory.length
        if (this.currentCategoryIndex == index) {
          this.currentPage++
          if (this.currentPage > this.currentTotalPage) {
            this.currentPage = 1
          }
        }
        else {
          this.currentCategoryIndex = index
          this.currentPage = 1
        }
        this.currentShowProducts = currentProductsOfCategory[this.currentPage - 1]
      },
    },
    beforeClose() {
      this.startLoop()
    }
  }
</script>

<style scoped lang="scss">
  * {
    user-select: none;
  }

  .page {
    width: 100%;
    height: 100%;
    display: flex;
  }
  .start {
    text-align: center;
    margin: auto;
    cursor: pointer;
    font-size: 80px;
    padding: 80px;
    box-shadow: 0 0 20px #000;
    border-radius: 20px;
    transition: all 0.3s;
  }
  .start:hover {
    background-color: #333;
    color: #fff;
  }
  .contentContainer {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    &.active {
      z-index: 1;
    }
  }
  .video {
    min-width: 100%;
    min-height: 100%;
  }

  .top {
    height: 120px;
    display: flex;

    .text {
      margin: auto;
    }

    h1 {
      text-align: center;
      font-size: 40px;
      color: rgb(33, 172, 55);
      padding-bottom: 10px;
    }

    .category {
      display: flex;

      li {
        padding: 5px 10px;
        cursor: pointer;
        border-radius: 5px;
        transition: all 0.3s;

        &.active {
          background-color: rgb(33, 172, 55);
          color: #fff;
          border: solid 1px rgb(33, 172, 55);
        }
      }
    }

    p {
      text-align: center;
      font-size: 24px;
    }
  }

  .wrapper {
    display: flex;
    height: calc(100% - 120px);

    .table {
      width: 50%;
      height: 100%;
      border-top: solid 1px #333;

      &.table1 {
        .cell {
          border-left: solid 1px #333;

        }
      }
    }

    .cell {
      cursor: pointer;
      position: relative;
      height: 10%;
      display: block;
      border-right: solid 1px #333;
      border-bottom: solid 1px #333;

      .l {
        text-align: left;
        position: absolute;
        top: 50%;
        left: 20px;
      }

      .r {
        text-align: right;
        position: absolute;
        right: 20px;
        top: 50%;

        i {
          font-style: normal;
          font-size: 18px;
        }
      }

      div {
        transform: translateY(-50%);

        span {
          font-size: 200%;
          line-height: 36px;
          display: block;
          margin-top: auto;
          margin-bottom: auto;
          font-weight: bold;
        }
      }
    }
  }
</style>